.menu-fixed {
  padding: 36px 0;
  background-color: $black;
  height: 100vh;
  width: 84px;
  position: fixed;
  left: 0;
  top: 0;
  @include media("<huge") {
    width: 74px; }
  @include media("<large") {
    display: none; }


  &__nav {
    margin: auto;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 72px); }

  &__button-wrapper {
    margin-top: auto;
    margin-bottom: 70px;
    position: relative;
    width: 84px;
    height: 200px;
    @include media("<huge") {
      height: 190px; } }


  &__order-call-btn {
    transform: rotate(270deg) translate(-74px, -58px);
    position: absolute;
    left: 0;
    top: 0;
    width: 200px;
    height: 46px;
    font-weight: 500;
    letter-spacing: 2px;
    @include media("<huge") {
      transform: rotate(270deg) translate(-74px, -48px);
      height: 42px;
      padding: 14px 15px;
      width: 180px; } }

  &__link {
    margin-top: 24px;
    svg {
      transition: $transition; }
    &--main {
      margin-top: 0;
      svg {
        fill: $white;
        transition: none;
        @include media("<huge") {
          height: 176px; } } }
    &--telegram, &--whatsapp {
      svg {
        fill: $maincolor; }
      &:hover {
        svg {
          fill: darken($maincolor, 20%); } } } } }
