.slides {
  background-color: $maincolor;
  overflow: hidden;
  width: 100%;
  position: relative;

  .main-title, .main-subtitle {
    position: absolute;
    z-index: 3;
    font-family: "Yeseva One", sans-serif;
    color: $white;
    text-align: right; }

  .main-title {
    right: 387px;
    top: calc(100vh - 572px);
    @include media("<huge") {
      top: 200px;
      top: calc(100vh - 450px); }
    @include media("<tablet") {
      top: 30px;
      right: 40px;
      max-height: 50px; }
    &__title {
      @include resetmargin;
      margin-bottom: 6px;
      font-size: 9.6rem;
      font-weight: normal;
      @include media("<huge") {
        font-size: 7.2rem; }
      @include media("<tablet") {
        font-size: 4.8rem; }
      @include media("<phone") {
        font-size: 4.5rem; } } }


  .main-subtitle {
    width: 750px;
    height: 252px;
    position: absolute;
    right: 387px;
    top: calc(100vh - 450px);
    @include resetmargin;
    font-weight: normal;
    font-size: 3.2rem;
    line-height: 46px;
    @include media("<huge") {
      font-size: 2.9rem;
      height: 227px;
      right: 340px;
      top: calc(100vh - 350px); }
    @include media("<desktop") {
      width: 400px; }
    @include media("<tablet") {
      position: absolute;
      width: 100%;
      height: unset;
      right: 0;
      top: 88px; }
    @include media("<phone") {
      font-size: 1.8rem; } }


  .main-subtitle__promotion-link {
    text-decoration: none;
    color: $white;
    pointer-events: none;
    opacity: 0;
    transition: opacity $transition/2;
    @include media("<huge");
    @include media("<large") {
      font-size: 2.8rem;
      line-height: 38px;
      .custom-underline {
        text-decoration: underline rgba($white, 0.5);
        max-width: 410px;
        display: block;
        position: absolute;
        right: 0;
        &:after {
          display: none; } }
      span:nth-of-type(2) {
        top: 76px; } }
    @include media("<tablet") {
      padding-right: 40px;
      box-sizing: border-box;
      width: calc(100% - 40px);
      display: flex;
      flex-flow: column;
      align-items: flex-end;
      line-height: 28px;
      font-size: 1.9rem;
      .custom-underline {
        display: none;
        //max-width: 300px
 }        //top: 10px
      span:nth-of-type(2) {
        top: 66px; } }
    @include media("<tiny") {
      .custom-underline {
        max-width: calc(100% - 50px); } }
    @include media("<phone") {
      font-size: 1.6rem; }
    .mobile-text {
      display: none;
      @include media("<tablet") {
        text-decoration: underline rgba($white, 0.5);
        display: block;
        max-width: 400px;
        width: calc(100% - 50px);
        position: absolute;
        top: 0;
        right: 0; } }

    &--activate {
      opacity: 1;
      pointer-events: all;
      transition: opacity $transition 1000ms; }

    a {
      display: block;
      position: absolute;
      right: 0;
      bottom: 0;
      width: max-content;
      margin-left: auto;
      margin-top: 40px;
      padding: 5px 38px;
      text-decoration: none;
      color: $white;
      font-family: "Open Sans", sans-serif;
      font-weight: 300;
      font-size: 1.2rem;
      text-transform: uppercase;
      letter-spacing: 2px;
      border: 2px solid $white;
      transition: background-color $transition;
      &:hover {
        background-color: rgba($white, 0.2); }
      @include media("<large") {
        padding: 9px 38px; }
      @include media("<tablet") {
        position: static;
        margin-top: 10px;
        font-size: 1rem;
        padding: 0 10px; } } }


  .main-cursive {
    height: 691px;
    width: 150px;
    position: absolute;
    z-index: 2;
    left: 63px;
    bottom: 0;
    @include media("<large") {
      display: none; }
    &__slide {
      width: 205px; }
    &__slide-text {
      @include resetmargin;
      position: absolute;
      bottom: -170px;
      left: 0;
      transform: rotate(-90deg);
      transform-origin: top left;
      font-size: 13rem;
      white-space: nowrap;
      color: $maincolor;
      font-family: "Northwell", cursive;
      @include media("<huge") {
        font-size: 12rem; } } } }


.master-slider {
  width: 100%;
  height: calc(100vh - 84px);
  transition: opacity .6s ease, -webkit-transform .3s ease;
  transition: opacity .6s ease, transform .3s ease;
  transition: opacity .6s ease, transform .3s ease, -webkit-transform .3s ease;
  @include media("<huge") {
    height: calc(100vh - 74px); }
  @include media("<tablet") {
    height: 672px; }
  .swiper-pagination-bullets {
    width: auto;
    right: 50px;
    top: 65px;
    left: auto;
    @include media("<tablet") {
      top: 250px;
      right: 40px; }
    .swiper-pagination-bullet {
      background-color: transparent;
      opacity: 1;
      margin: 0 6px;
      position: relative;
      height: 26px;
      width: 26px;
      &:focus {
        outline: none; } }
    .swiper-inside-bullet {
      position: absolute;
      display: block;
      width: 8px;
      height: 8px;
      background-color: $white;
      border-radius: 50%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      mix-blend-mode: difference; } }

  .swiper-container {
    width:  100%;
    height: calc(100vh - 84px);
    @include media("<tablet") {
      height: 700px; } }
  .swiper-wrapper {
    position: relative; }
  .slide-zoom {
    animation-name: slide-zoom;
    animation-duration: 7000ms;
    animation-fill-mode: forwards; }

  .swiper-slide {
    overflow: hidden;
    filter: brightness(75%);
    transform: scale(1);
    .slide-fig {
      margin: 0;
      padding: 0;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: cover;
      img {
        object-fit: cover;
        width: 100%;
        height: 100%; } } }
  .slide-zoom {
    animation-name: slide-zoom;
    animation-duration: 7000ms;
    animation-fill-mode: forwards; } }

@keyframes slide-zoom {
  from {
    transform: scale(1); }
  to {
    transform: scale(1.1); } }

