  .slide-zoom {
    animation-name: slide-zoom;
    animation-duration: 7000ms;
    animation-fill-mode: forwards; } }

@keyframes slide-zoom {
  from {
    transform: scale(1); }
  to {
    transform: scale(1.1); } }


#block-search-main {
  position: absolute;
  right: 84px;
  bottom: 76px;
  background: #fff;
  border-radius: 5px;
  width: 100%;
  max-width: 245px;
  z-index: 5;
  @include media("<huge") {
    max-width: 222px;
    max-height: 348px;
    bottom: unset;
    top: calc(100vh - 450px); }
  @include media("<tablet") {
    right: 40px;
    top: 300px; } }

.tl-container {
  padding: 0 36px;
  @include media("<huge") {
    margin-top: -30px;
    padding: 0 24px; } }
