.collection-header {
  padding: 160px 15px 165px;
  position: relative;
  z-index: 1;
  color: $white;
  background-color: $dark-text;
  &::after {
    content: "";
    display: block;
    position: absolute;
    opacity: 0.3;
    filter: brightness(75%);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1;
    background-size: cover;
    background-position: center; }
  @include media('<large') {
    padding: 120px 15px 125px; }
  @include media('<tablet') {
    padding: 80px 15px 85px; }


  &--restaurants {
    &::after {
      background-image: url('../../media/static/img/restaurants-bg-img.jpg'); } }
  &--spa {
    &::after {
      background-image: url('../../media/static/img/spa-bg-img.jpg'); } }
  &--services {
    &::after {
      background-image: url('../../media/static/img/services-bg-img.jpg'); } }
  &--rooms {
    &::after {
      background-image: url('../../media/static/img/rooms-bg-img.jpg'); } }
  &--rooms-luxe {
    &::after {
      background-image: url('../../media/static/img/luxe-bg-img.jpg'); } }
  &--specials {
    &::after {
      background-image: url('../../media/static/img/specials-bg-img.jpg'); } }
  &--booking {
    padding: 100px 15px 100px;
    &::after {
      background-image: url('../../media/static/img/luxe-bg-img.jpg'); } }
  &--cursive {
    background-color: $section-bg;
    color: $black;
    overflow: hidden;
    .collection-header__title {
      font-size: 3.6rem; }
    .collection-header__text {
      color: $dark-text; }
    .btn {
      padding: 20px;
      border: none;
      display: block;
      width: 357px;
      margin: 45px auto 10px;
      color: $white;
      max-width: 100%;
      &:hover {
        color: $black; }
      @include media('<large') {
        &:hover {
          color: $white; } } } }

  &__cursive {
    font-size: 18rem;
    font-family: 'Northwell', cursive;
    line-height: 100%;
    color: $maincolor;
    position: absolute;
    bottom: -85px;
    width: 100%;
    text-align: center;
    @include media('<large') {
      font-size: 13rem;
      bottom: -45px; }
    @include media('<desktop') {
      display: none; } }
  &__wrapper {
    margin: auto;
    @include media('<large') {
      max-width: 456px; }
    @include media('<tablet') {
      max-width: 95%; } }


  &__title {
    @include resetmargin;
    margin-top: 30px;
    font-size: 5.4rem;
    font-family: "Yeseva One", serif;
    font-weight: normal;
    text-align: center;
    @include media('<large') {
      font-size: 4.8rem; }
    @include media('<tablet') {
      font-size: 3.6rem; } }


  &__text {
    @include resetmargin;
    margin: 30px auto 0;
    font-family: "Open Sans", sans-serif;
    font-size: 1.8rem;
    line-height: 156%;
    text-align: center;
    max-width: 680px;
    @include media('<large') {
      font-size: 1.6rem; }
    @include media('<tablet') {
      font-size: 1.4rem; } } }

